.Navbar {
    background-color: #003566;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;  
    color: #FFC300;
    max-height: 100px;
  }
  nav{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    padding: 0 20px;
  }
  nav ul{
    display: flex;
    list-style: none; 
    align-items: center;
    justify-content: space-between;
    padding-left: 0;
  }
  .nav-left{
    width: 30%;
  }
  .nav-right{
    min-width: 15%;
  }
  nav li{
    display: inline;
    padding: 10px;
    font-size: 20px;
    font-weight: bold;
    text-transform: uppercase;
    cursor: pointer;
  }
  nav a{
    color: #FFC300;
    text-decoration: none;
  }
  nav a:hover:not(#logo a){
    color: #FFD700;
  }
  #logo{
    padding-left: 0;
  }
  #logo img{
    width: 93px;
    margin-right: -13px;
  }
  #logo a{
    display: flex;
    flex-direction: row;
    align-items: center;
    font-family: 'Luckiest Guy', cursive;
    font-size: 23px;
  }
  
  nav button{
    background-color: #FFC300;
    color: #003566;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
    transition: 0.3s;
  }
  nav button:hover{
    background-color: #FFD700;
  }
.nav-right i{
    font-size: 40px;
  }